import {Business} from '@/interfaces'

export const userLocale = navigator.language
export const currencyStyle = {style:"currency", currency: "EUR"}

export function language(business: Business): string {
    const userLanguage: string = navigator.language.split('-')[0];
    if (business.translations.includes(navigator.language)) {
        return userLanguage;
    } else {
        return "en";
    }
}

